*{
    box-sizing: border-box;
    margin: 0px;
}
body{
    display:flex;
    justify-content: center;
    color:white;
    overflow: auto;
}
#outer-div{
    width:550px;
    height:868px;
    background-color: #23252C;
    display:flex;
    justify-content: center;
    align-items: center;
}

#inner-div{
    width:317px;
    height:780px;
    background-color: #00000040;
    border: 1px solid #00000040;
    box-shadow: 0px 4px 4px 0px #00000040;
    border-radius: 1.2rem;
}

img{
    width:100%;
    height:317px;
    object-fit: cover;
    border-top-left-radius: 1.2rem;
    border-top-right-radius: 1.2rem;
}
#main{
    width: 317px;
    height: 398px;
    text-align: center;
    padding: 21px 30px;
}
#main h2{
    margin-bottom: 7px;
}
#designation{
    margin-bottom: 15px;
    font-size: 12.8px;
    color: #F3BF99;
    
}
.button{
    margin-bottom: 33px;
    display: flex;
    justify-content: space-evenly;
    
}
button{
    width: 115px;
    height: 34px;
    border-radius: 6px;
    font-size: 14px;
    
}
.fa{
    margin: 8px;
    font-size: 13px;
    
}
.fa-linkedin{
    color:#5093E2;
    background-color: white;
    padding: 1px;  
     
}
.fa-linkedin, .fa-envelope{
    border-radius: 1.5px;
}
#linkedin-btn{
    background-color:#5093E2 ;
    color: #FFFFFF;
    border: none;
}
#email-btn{
    border: 1px solid #D1D5DB;
}
.info{
    width: 247px;
    height:150px;
    text-align: start;
    
}
.info h3{
    margin-bottom: 5px;
}
.info p{
    color:#DCDCDC;
    font-size: 10.24px;
    line-height: 15.36px;
   
}
footer{
    background-color: #161619;
    display: flex;
    justify-content: center;
    padding: 9px;
    border-radius: 1.2rem;
}
.fa-facebook, .fa-twitter,.fa-instagram,.fa-github{
    background-color:#918E9B;
    color: black;
    font-size: 20px;
    width: 25px;
    height: 25px;
    text-align: center;
    padding-top: 2.5px;
    border-radius: 5px;
}